import { fetchRepos } from "./_utils/Services";

export default async function Home() {
  const [error, data] = await fetchRepos()
  return (
    <main className="flex flex-col items-center justify-between">
      {/* //<SearchInput search={searchParams.search} queryString={searchParams} /> */}
      {
        !error && data?.count > 0 && data.results.length
          ? (
              <div className='mt-4'>
                {
                  (data?.results || []).map(({ name, url }: { name: string, url: string }, index: number) => (
                    <ListCard key={index} title={name} description={url} isClicked />
                  ))
                }
              </div>
          )
          : <DefaultMessage />
      }
      {
        error ? <DefaultMessage message="Something went wrong!" /> : null
      }
    </main>
  );
}
